
import { Vue, axios } from "./";
import { SteamRepositoryAxios } from "../../shared/axios/SteamRepositoryAxios";
const repo = new SteamRepositoryAxios(axios);
export default Vue.extend({
  name: "CreateNewUser",
  data() {
    return {
      username: "",
      password: "",
      password2: "",
      usernameAvailability: false,
    };
  },
  methods: {
    backToHome() {
      this.$router.push("/games");
    },
    async checkUsernameAvailability(username: string) {
      var response = false;
      try {
        response = await repo.checkUserNameAvailability(username);
      } catch (error) {
        this.usernameAvailability = false;
        return false;
      }
      this.usernameAvailability = response;
    },
    async CreateNewUser() {
      if (this.password !== this.password2) {
        alert("Passwords do not match");
        return;
      }
      if (
        this.username === "" ||
        this.password === "" ||
        this.password2 === ""
      ) {
        alert("Fields need to be filled out");
        return;
      }
      if (!this.usernameAvailability) {
        alert("this Username has been taken");
        return;
      }
      await repo.createNewUser(this.username, this.password);
      this.$router.push("/login");
    },
    goToLogin() {
      this.$router.push("/login");
    },
  },
  computed: {
    usernameAvailable(): string {
      return this.usernameAvailability
        ? "UsernameAvailable"
        : "UsernameNotAvailable";
    },
  },
});
